import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { CalendarPageComponent } from './components/calendar-page/calendar-page.component';
import { EventCardPageComponent } from './components/event-card-page/event-card-page.component';
import { GroupsResolver } from 'src/app/services/security/groups.resolver';

const routes: Routes = [
  {
    path: '',
    component: CalendarPageComponent,
    resolve: { groups: GroupsResolver },
    data: { animation: 'fadeAnimation', title: 'Календарь событий' }
  },
  {
    path: ':id',
    component: EventCardPageComponent,
    resolve: { groups: GroupsResolver },
    data: { animation: 'fadeAnimation', title: 'Календарь событий' }
  }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    providers: [ GroupsResolver ],
    exports: [RouterModule]
})
export class CalendarRoutingModule { }
