import { Component, OnInit } from '@angular/core';

import * as moment from 'moment';

import { SidebarService } from 'src/app/services/sidebar/sidebar.service';

@Component({
  selector: 'app-calendar-page',
  templateUrl: './calendar-page.component.html',
  styleUrls: ['./calendar-page.component.scss']
})
export class CalendarPageComponent implements OnInit {

  public months: string[] = null;
  public date: Date = null;
  public month: number = null;
  public year: number = null;
  public active: number = null;

  public days = ['Пн', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб', 'Вс'];

  public numbers = [];

  legends = [{
      mark: '#da402b',
      name: 'События компании'
    },
    {
      mark: '#27EE2F',
      name: 'События комбината'
    },
    {
      mark: '#B016CC',
      name: 'События в отрасли'
    },
    {
      mark: '#EEA127',
      name: 'События подразделения'
    }];

  events = [
    {
      id: 1,
      title: 'Встреча с компанией Spellabs',
      place: 'МГОК',
      timeStart: '14:30',
      timeEnd: '16:00',
      mark: '#da402b'
    },
    {
      id: 2,
      title: 'Выставка Global Metal 2018',
      place: 'МГОК',
      timeStart: '10:00',
      timeEnd: '',
      mark: '#B016CC'
    }
   ];

  constructor(private sidebarService: SidebarService) {
    this.sidebarService.handleSetSettings({ showSidebar: false, bgColor: '#fff' });
    this.months = Array.apply(0, Array(12)).map((v, i: number) => moment().month(i).format('MMMM'));
    this.resetDate();
  }

  ngOnInit() {

  }

  getEventsByMonth() {

  }

  setDate(month: number, year: number) {
    this.month = month;
    this.year = year;
    this.date.setDate(1);
    this.date.setMonth(month);
    this.date.setFullYear(year);
    this.numbers = [];
    for (let i = 0; i < moment(this.date).isoWeekday(); i++) {
      this.numbers.push();
    }
    for (let i = 1; i <= moment(this.date).daysInMonth(); i++) {
      this.numbers.push(i);
    }
  }

  resetDate() {
    this.date = new Date();
    this.setDate(this.date.getMonth(), this.date.getFullYear());
  }
}
